import { CalendarDate } from './calendarDate';
import { formatDateString, getDayOfWeek, getMonthString, getWeekForDate } from './calendarUtils';
import { CalendarType } from './calendarType';
import { CalendarLang } from './calendarLang';

export class CalendarService {
    // Private members
    private _now: Date;
    private _selectedDate: Date | null = null;
    private readonly _lang: CalendarLang;
    private _cache: any = {};

    // Getters
    get now() {
        return this._now;
    }

    get selectedDate() {
        return this._selectedDate;
    }

    get selectedWeek() {
        if (typeof this._cache.selectedWeek === 'undefined') {
            const { selectedDate } = this;
            this._cache.selectedWeek = selectedDate ? getWeekForDate(selectedDate) : null;
        }
        return this._cache.selectedWeek;
    }

    // Public methods
    constructor(now?: Date, lang = CalendarLang.Sv) {
        this._now = now || new Date();
        this._lang = lang;
    }

    setNow(date: Date): void {
        this._now = date;
        this._cache = {};
    }

    setSelectedDate(date: Date | null) {
        this._selectedDate = date ? new Date(date) : null;
        this._cache = {};
    }

    getMonth(): number {
        return this._now.getMonth();
    }

    getMonthString(offset = 0): string {
        let date = this.now;

        if (offset !== 0) {
            date = new Date(this.now);
            date.setMonth(date.getMonth() + offset);
        }

        return getMonthString(date, this._lang);
    }

    getYear(): number {
        return this._now.getFullYear();
    }

    getYearString(offset = 0): string {
        let year = this.getYear();

        if (offset !== 0) {
            const date = new Date(this.now);
            date.setFullYear(year + offset);
            year = date.getFullYear();
        }

        return year.toString();
    }

    selected = (d) =>
        !!this.selectedDate &&
        formatDateString(this.selectedDate, CalendarType.Date) === formatDateString(d, CalendarType.Date);

    getDatesOfMonth(): CalendarDate[] {
        if (typeof this._cache.getDatesOfMonth === 'undefined') {
            const month = this.now.getMonth();
            const dates = [...new Array(31)]
                .map((_, i) => {
                    const d = new Date(this.now);
                    d.setDate(i + 1);

                    if (d.getMonth() === month) {
                        return new CalendarDate(d, { selected: this.selected(d), lang: this._lang });
                    } else {
                        return null;
                    }
                })
                .filter(Boolean);

            this._cache.getDatesOfMonth = dates as CalendarDate[];
        }

        return this._cache.getDatesOfMonth;
    }

    getOffsetDatesOfPrevMonth(offset) {
        if (typeof this._cache.getOffsetDatesOfPrevMonth === 'undefined') {
            if (offset > 0) {
                const dates = [...new Array(offset)].map((_, i) => {
                    const d = new Date(this.now);
                    d.setDate(-offset + i + 1);
                    return new CalendarDate(d, { selected: this.selected(d), prev: true });
                });
                this._cache.getOffsetDatesOfPrevMonth = dates as CalendarDate[];
            } else {
                this._cache.getOffsetDatesOfPrevMonth = [];
            }
        }
        return this._cache.getOffsetDatesOfPrevMonth;
    }

    getOffsetDatesOfNextMonth(rest) {
        if (typeof this._cache.getOffsetDatesOfNextMonth === 'undefined') {
            if (rest > 0) {
                const dates = [...new Array(rest % 7)].map((_, i) => {
                    const d = new Date(this.now);
                    d.setMonth(d.getMonth() + 1);
                    d.setDate(i + 1);
                    return new CalendarDate(d, { selected: this.selected(d), next: true });
                });
                this._cache.getOffsetDatesOfNextMonth = dates as CalendarDate[];
            } else {
                this._cache.getOffsetDatesOfNextMonth = [];
            }
        }
        return this._cache.getOffsetDatesOfNextMonth;
    }

    getDateOffsetOfMonth() {
        if (typeof this._cache.getDateOffsetOfMonth === 'undefined') {
            const firstDateOfMonth = new Date(this.now);
            firstDateOfMonth.setDate(1);
            this._cache.getDateOffsetOfMonth = getDayOfWeek(firstDateOfMonth);
        }

        return this._cache.getDateOffsetOfMonth;
    }
}
