export class CalendarTexts {
    previousMonth: string;
    nextMonth: string;
    currentlySelected: string;
    previousYear?: string;
    nextYear?: string;
    month?: string[];
    day?: string[][];

    constructor(
        previousMonth: string,
        nextMonth: string,
        currentlySelected: string,
        previousYear?: string,
        nextYear?: string,
        month?: string[],
        day?: string[][]
    ) {
        this.previousMonth = previousMonth;
        this.nextMonth = nextMonth;
        this.currentlySelected = currentlySelected;
        this.previousYear = previousYear;
        this.nextYear = nextYear;
        this.month = month;
        this.day = day;
    }

    static emptyTexts(): CalendarTexts {
        return {
            previousMonth: 'Previous month',
            nextMonth: 'Next month',
            currentlySelected: 'Currently selected',
            previousYear: 'Previous year',
            nextYear: 'Next year',
            month: [
                'January',
                'February',
                'March',
                'April',
                'May',
                'June',
                'July',
                'August',
                'September',
                'October',
                'November',
                'December',
            ],
            day: [
                ['Monday', 'Mon'],
                ['Tuesday', 'Tue'],
                ['Wednesday', 'Wed'],
                ['Thursday', 'Thu'],
                ['Friday', 'Fri'],
                ['Saturday', 'Sat'],
                ['Sunday', 'Sun'],
            ],
        };
    }
}
