import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

const cssNoMin = css; // html-minifier-terser fail!

const INNER_SPACING = 8;

const BUTTON_HOVER = 'button:hover';
const notOngoingSelectionEndConnection = '!ongoingSelectionEndConnection';
const notOngoingSelectionStartConnection = '!ongoingSelectionStartConnection';
const startNodeInSpan = 'startNodeInSpan';
const notStartNodeInSpan = '!startNodeInSpan';
const notStartNode = '!startNode';
const notEndNodeInSpan = '!endNodeInSpan';
const endNodeInSpan = 'endNodeInSpan';

function hostDefaults() {
    return css`
        :host,
        :host::before,
        :host::after,
        :host *,
        :host *::before,
        :host *::after {
            box-sizing: border-box;
        }
        :host {
            display: block;
        }
    `;
}

function generateDateStyles(tagName: string) {
    const { root, child, is } = cssSelector(`${tagName}-date`);

    return cssNoMin`
        ${root} {
            position: relative;
            width: 72px;
            height: 48px;
        }

        ${root}::before {
            content: '';
            display: block;
            inset: 0px;
            position: absolute;
        }

        ${is('void')} {
            height: 0;
            opacity: 0;
            pointer-events: none;
        }
  
        ${child('inner')} {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }

        ${child('inner::before')} {
            content: '';
            position: absolute;
            inset: 0;
            background-color: ${cssVariableAsVar('color_secondary_1')};
        }
        
        ${is('prev')} ${child('inner::before')},
        ${is('next')} ${child('inner::before')}{
            background: 0;
        }
        
        ${is(startNodeInSpan, notEndNodeInSpan)} ${child('inner::before')},
        ${is(endNodeInSpan, notStartNodeInSpan)} ${child('inner::before')},
        ${is('ongoingSelectionStartConnection', notOngoingSelectionEndConnection)} ${child('inner::before')},
        ${is('ongoingSelectionWithin')} ${child('inner::before')},
        ${is(
            'ongoingSelectionEndConnection',
            notOngoingSelectionStartConnection,
            notStartNodeInSpan,
            notStartNode
        )} ${child('inner::before')} {
            background-color: ${cssVariableAsVar('color_secondary_3')};
        }

        ${is('within')} ${child('inner::before')} {
            background-color: ${cssVariableAsVar('color_primary_3')};
        }

        ${is('within')} ${child('inner::before')},
        ${is('ongoingSelectionWithin')} ${child('inner::before')} {
            left: 0;
            width: 100%;
        }

        ${is('today')} ${child('inner::after')} {
            content: '';
            position: absolute;
            bottom: 0;
            left: calc(50% - 26px);
            bottom: 10px;
            z-index: 1;
            width: 52px;
            height: 2px;
            background-color: ${cssVariableAsVar('color_primary_3')};
        }
        ${is('today', 'selected')} ${child('inner::after')},
        ${is('today', 'startNode')} ${child('inner::after')},
        ${is('today', 'endNode')} ${child('inner::after')} {
            background-color: ${cssVariableAsVar('color_primary_1')};
        }

        ${child('button')} {
            display: block;
            position: relative;
            width: 100%;
            height: 100%;
            margin: 0;
            padding: 0;
            background: 0;
            color: ${cssVariableAsVar('color_primary_3')};
            font-weight: bold;
            font-size: 14px;
            outline: 0;
            border: 0;
            border-radius: 0%;
        }

        ${is('!disabled')} ${child('button')} {
            cursor: pointer;
        }

        ${is('within')} ${child('button')},
        ${is('endNode')} ${child('button')},
        ${is('startNode')} ${child('button')},
        ${is('selected')} ${child('button')},
        ${is(startNodeInSpan)} ${child('button')},
        ${is(endNodeInSpan, notStartNodeInSpan)} ${child('button')} {
            background-color: ${cssVariableAsVar('color_primary_3')};
            color: ${cssVariableAsVar('color_primary_2')};
        }

        ${is('selected', '!start', '!end')} ${child('button')} {
            border-bottom: 4px solid ${cssVariableAsVar('color_primary_1')};
        }
        ${is('startNode')} ${child('button')},
        ${is('selected', 'start')} ${child('button')} {
            border-left: 4px solid ${cssVariableAsVar('color_primary_1')};
        }
        
        ${is('endNode')} ${child('button')},
        ${is('selected', 'end')} ${child('button')} {
            border-right: 4px solid ${cssVariableAsVar('color_primary_1')};
        }

        ${is('!disabled', '!selected', notStartNodeInSpan, notEndNodeInSpan, notOngoingSelectionEndConnection)} ${child(
            BUTTON_HOVER
        )},
        ${is('!disabled', '!selected', 'within')} ${child('button:focus-visible')} {
            background-color: ${cssVariableAsVar('color_secondary_4')};
            color: ${cssVariableAsVar('color_primary_2')};
        }

        /* Hover when selecting span */
        ${is(
            '!disabled',
            '!selected',
            '!within',
            notStartNode,
            notOngoingSelectionStartConnection,
            'ongoingSelectionEndConnection'
        )} ${child(BUTTON_HOVER)} {
            background-color: ${cssVariableAsVar('color_secondary_3')};
        }

        /* Hover on an already selected span */
        ${is('!disabled', 'within', 'ongoingSelectionEndConnection')} ${child(BUTTON_HOVER)} {
            opacity: 0.75;
        }

        ${is('!disabled')} ${child('button:focus-visible')} {
            border: 1px solid ${cssVariableAsVar('color_primary_3')};
        }

        ${is('!disabled', 'selected')} ${child('button:focus-visible')} {
            background-color: ${cssVariableAsVar('color_secondary_4')};
        }

        ${child('label')} {
            position: relative;
            pointer-events: none;
        }

        ${is('disabled', notStartNodeInSpan, notEndNodeInSpan, notStartNode)} ${child('label')} {
            color: ${cssVariableAsVar('color_secondary_4')};
            font-weight: normal;
        }
    `;
}

export const calendarStyle: GenerateStyles = (tagName) => {
    const { root, child } = cssSelector(tagName);

    return [
        style,
        hostDefaults(),
        css`
            :host {
                display: inline-block;
            }

            ${root} {
                width: 100vw;
                max-width: 513px;

                background-color: ${cssVariableAsVar('color_primary_2')};
                border: 1px solid ${cssVariableAsVar('color_secondary_3')};

                user-select: none;
            }

            ${child('nav')} {
                display: flex;
                justify-content: space-between;
                align-items: center;
                min-height: 40px;
                background-color: ${cssVariableAsVar('color_secondary_2')};
                white-space: nowrap;
            }
            ${child('nav-title')} {
                overflow: hidden;
                font-size: 14px;
            }

            ${child('days')} {
                display: flex;
                margin: ${INNER_SPACING}px 0;
            }

            ${child('day')} {
                flex: 1;
                line-height: 19px;
                color: ${cssVariableAsVar('color_primary_3')};
                font-size: 14px;
                text-align: center;
            }

            ${child('dates')} {
                display: flex;
                flex-flow: row wrap;
                gap: 1px;
            }
        `,
        generateDateStyles(tagName),
    ];
};
