import {
    BaseElement,
    booleanConverter,
    cache,
    createClassName,
    defineElement,
    html,
    property,
    state,
} from '@umetrics/sartorius-ui-base';

import back from '@umetrics/sartorius-ui-icon/dist/icons/back';
import next from '@umetrics/sartorius-ui-icon/dist/icons/next';
import { calendarStyle } from './calendarStyle';
import { CalendarProperties } from './calendarProperties';
import { CalendarService } from './calendarService';
import { CalendarEvent, CalendarEventType } from './calendarEvent';
import { CalendarCustomizeDate } from './calendarCustomizeDate';
import { CalendarType } from './calendarType';
import { CalendarDate } from './calendarDate';
import { CalendarLang } from './calendarLang';
import { daysByLang, monthsByLang } from './calendarStrings';
import { formatDateString, getMonthString, isDateDisabled } from './calendarUtils';
import { version } from './metadata';
import { CalendarTexts } from './calendarTexts';
import type { ContextMenuItems } from '@umetrics/sartorius-ui-context-menu';

function getFocusableProp(this: Calendar, cDate: CalendarDate, value, selected, disabled) {
    if (selected || (cDate.getDate() === this.dateExpectedFocus && (!value || value.getMonth() !== cDate.getMonth()))) {
        if (disabled) {
            this.dateExpectedFocus++;
            return false;
        } else {
            return true;
        }
    }
    return false;
}

function createCalendarDateProps(this: Calendar, cDate: CalendarDate | null) {
    const { value, customizeDate } = this;
    const custom = (!!cDate && customizeDate ? customizeDate(cDate) : null) as any;
    let today = false;
    let weekend = false;
    let disabled = false;
    let selected = false;
    let start = false;
    let end = false;
    let within = false;
    let focusable = false;
    let prevMonth = false;
    let nextMonth = false;

    if (cDate) {
        today = cDate.isToday();
        weekend = cDate.isWeekend();
        selected = cDate.isSelected();
        prevMonth = cDate.isPrev();
        nextMonth = cDate.isNext();
        disabled = !!custom && custom.disabled === true;
        start = !!custom && custom.__start === true;
        end = !!custom && custom.__end === true;
        within = !!custom && custom.__within === true;
        focusable = getFocusableProp.call(this, cDate, value, selected, disabled);
    }

    return {
        void: !cDate,
        today,
        weekend,
        disabled,
        selected,
        start,
        end,
        within,
        focusable,
        prev: prevMonth,
        next: nextMonth,
    };
}

const PREVIOUS_MONTH = 'prev-month';
const NEXT_MONTH = 'next-month';

@defineElement({
    tagName: 'calendar',
    styles: calendarStyle,
    version,
    //sub: [Icon, Button, ContextMenu, ContextMenuItem],
})
export class Calendar extends BaseElement implements CalendarProperties {
    // Static members
    private static ContextMenu;
    private static Button;
    private static Icon;

    // Public members
    @property({ type: CalendarType })
    type = CalendarType.Date;
    @property({ type: String })
    start = '';
    @property({ type: String })
    end = '';
    @property({ type: Function })
    customizeDate?: CalendarCustomizeDate;
    @property({ type: Boolean, attribute: 'start-is-selected-date', converter: booleanConverter })
    startIsSelectedDate = false;
    @property({ type: Boolean, attribute: 'end-is-selected-date', converter: booleanConverter })
    endIsSelectedDate = false;

    @property({ type: Date })
    get now() {
        return this._now;
    }

    set now(value) {
        const prevValue = this.now;

        if (!value) {
            value = new Date();
        }
        this._service.setNow(value);
        this._now = value;
        this.requestUpdate('now', prevValue);
    }

    @property({ type: Date, attribute: 'value', reflect: true })
    get value() {
        return this._value;
    }

    set value(value) {
        const prevValue = this.value;
        if (prevValue !== value) {
            this._service.setSelectedDate(value);
            this._value = value;
            this.requestUpdate('value', prevValue);
        }
    }

    @property({ type: CalendarLang })
    get lang() {
        return this._lang;
    }

    set lang(value) {
        const prevLang = this.lang;

        if (value !== prevLang) {
            this._lang = value;
            this._service = new CalendarService(this.now, value);
            this.requestUpdate('lang', prevLang);
        }
    }

    @property({ type: CalendarTexts })
    get texts() {
        return this._texts;
    }

    set texts(texts) {
        const prevTexts = this._texts;

        if (typeof texts === 'string') {
            texts = JSON.parse(texts);
        }

        this._texts = texts;
        this.requestUpdate('texts', prevTexts);
    }

    @property({ type: Boolean, attribute: 'no-month-navigation', converter: booleanConverter })
    noMonthNavigation = false;
    @property({ type: Boolean, attribute: 'show-offset-days', converter: booleanConverter })
    showOffsetDays = false;

    // Private members
    private _baseClassName = '';
    private _lang = CalendarLang.En;
    private _now = new Date();
    private _value: Date | null = null;
    private _service: CalendarService;
    private _dateExpectedFocus = -1;
    private _mouseOutTimer = -1;
    private _texts: CalendarTexts = CalendarTexts.emptyTexts();

    // Protected members
    @state()
    private _focusedDate: Date | null = null;
    @state()
    private _contextMenu = '';

    // Public methods
    constructor() {
        super();
        this._service = new CalendarService(this.now, this.lang);
    }

    get dateExpectedFocus() {
        return this._dateExpectedFocus;
    }

    set dateExpectedFocus(value) {
        this._dateExpectedFocus = value;
    }

    focus(options?: FocusOptions) {
        const element = this._getFirstFocusableDateButtonElement();
        if (element) {
            element.focus(options);
        }
    }

    blur() {
        const { shadowRoot } = this;
        const activeElement = (shadowRoot && shadowRoot.activeElement) as HTMLElement;

        if (activeElement) {
            activeElement.blur();
        }
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];

        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: 'Button' });
        imports.push({
            module: () => import('@umetrics/sartorius-ui-context-menu'),
            elem: 'ContextMenu',
        });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: 'Icon' });

        Calendar.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    // Protected methods
    protected render() {
        const { type } = this;
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            [`type-${type}`]: true,
        });
        this._baseClassName = baseClassName;

        return html`
            <div class="${rootClasses}" @keydown=${this._onKeyDown}>
                ${this._renderNavigationButtons()} ${this._renderDaysOfWeekTemplate()} ${this._renderDatesTemplate()}
            </div>
        `;
    }

    // Private methods
    private _onKeyDown(e: KeyboardEvent) {
        const { key } = e;
        const target = e.target as HTMLElement;
        const isDateButton = target.classList.contains(`${this._baseClassName}-date-button`);

        if (!isDateButton) {
            return;
        }

        switch (key) {
            case 'Left':
            case 'ArrowLeft':
                e.preventDefault();
                this._focusDateByOffset(-1);
                break;
            case 'Right':
            case 'ArrowRight':
                e.preventDefault();
                this._focusDateByOffset(1);
                break;
            case 'Up':
            case 'ArrowUp':
                e.preventDefault();
                this._focusDateByOffset(-7);
                break;
            case 'Down':
            case 'ArrowDown':
                e.preventDefault();
                this._focusDateByOffset(7);
                break;
            default:
                break;
        }
    }

    private _renderNavigationButtons() {
        return html`
            <div class="${this._baseClassName}-nav">
                <div>${this._renderMonthNavButtonTemplate(PREVIOUS_MONTH)}</div>
                <div class="${this._baseClassName}-nav-title" id="id-dialog-label" aria-live="polite">
                    ${this._getTitle()}
                </div>
                <div>${this._renderMonthNavButtonTemplate(NEXT_MONTH)}</div>
            </div>
        `;
    }

    private _renderMonthNavButtonTemplate(direction: 'prev-month' | 'next-month') {
        if (this.noMonthNavigation) {
            return html``;
        }
        const icon = `${direction === PREVIOUS_MONTH ? back : next}`;
        return html`
            <${Calendar.Button.tag}
                class="${this._baseClassName}-button-${direction}"
                compact='true'
                type='tertiary'
                aria-label="${this._getMonthNavigationText(direction === PREVIOUS_MONTH ? -1 : 1)}"
                @click=${direction === PREVIOUS_MONTH ? this._goPrevMonth : this._goNextMonth}
            >
                <${Calendar.Icon.tag} class="${this._baseClassName}-icon-${direction}" icon="${icon}"
                                      slot='icon-slot'></${Calendar.Icon.tag}>
            </${Calendar.Button.tag}>
        `;
    }

    private _getTitle() {
        const date = this._service.now;
        const month = getMonthString(date, this.lang);
        const selectedYear = date.getFullYear();

        const year = new Date().getFullYear();

        let menu = html``;
        let items: undefined | ContextMenuItems;
        if (this._contextMenu) {
            if (this._contextMenu === 'month') {
                const currentIndex = date.getMonth();
                items = {
                    width: '100px',
                    virtualize: false, // 12 months only
                    selectedItem: currentIndex,
                    scrolledItem: currentIndex,
                    item: monthsByLang.en.map((label, index) => {
                        return {
                            label,
                            clickCallback: () => {
                                this._contextMenu = '';
                                this._changeMonth(index);
                            },
                        };
                    }),
                };
            } else {
                const stop = year + 10;
                const start = 1970;
                const step = 1;
                const currentIndex = selectedYear / step - start;
                items = {
                    width: '100px',
                    virtualize: true,
                    selectedItem: currentIndex,
                    scrolledItem: currentIndex,
                    item: Array.from(
                        { length: (stop - start) / step + 1 },
                        (_, index) => '' + (start + index * step)
                    ).map((label) => {
                        return {
                            label,
                            clickCallback: () => {
                                this._contextMenu = '';
                                this._changeYear(label);
                            },
                        };
                    }),
                };
            }
            menu = html`
                <${Calendar.ContextMenu.tag}
                    class='${this._baseClassName}-cc'
                    open=${this._contextMenu !== ''}
                    click-outside-close=${true}
                    object-id=${this._contextMenu === 'year' ? 'year-button' : 'month-button'}
                    .items=${items}
                    @close=${() => (this._contextMenu = '')}
                >
                </${Calendar.ContextMenu.tag}>
            `;
        }
        return html`
            <${Calendar.Button.tag}
                id='year-button'
                class="${this._baseClassName}-button-year"
                type='tertiary'
                aria-label="Change year"
                @click=${() => (this._contextMenu = !this._contextMenu ? 'year' : '')}
            >
                ${date.getFullYear()}
            </${Calendar.Button.tag}>
            <${Calendar.Button.tag}
                id='month-button'
                class="${this._baseClassName}-button-month"
                type='tertiary'
                aria-label="Change month"
                @click=${() => (this._contextMenu = !this._contextMenu ? 'month' : '')}
            >
                ${month}
            </${Calendar.Button.tag}>
            ${menu}
        `;
    }

    private _getMonthNavigationText(monthOffset = 0) {
        if (monthOffset < 0) {
            return this._texts.previousMonth;
        }
        return this._texts.nextMonth;
    }

    private _renderDaysOfWeekTemplate() {
        const { lang, _baseClassName: baseClassName } = this;
        const dayTemplates = daysByLang[lang].map(
            (day) => html` <div class="${baseClassName}-day" aria-label="${day[0]}">${day[1]}</div> `
        );

        return html` <div class="${baseClassName}-days">${dayTemplates}</div> `;
    }

    private _renderDatesTemplate() {
        const baseClassName = this._baseClassName;
        const service = this._service;
        const dates = service.getDatesOfMonth();
        const offset = service.getDateOffsetOfMonth();
        const rest = 42 - (offset + dates.length); // 42 = Highest amount of date columns
        const prevDates = this.showOffsetDays ? service.getOffsetDatesOfPrevMonth(offset) : Array(offset);
        const nextDates = this.showOffsetDays ? service.getOffsetDatesOfNextMonth(rest) : [];

        this.dateExpectedFocus = 1;

        const dateTemplates = [
            ...prevDates,
            ...dates,
            ...nextDates,
            ...Array((rest > 0 ? rest : 0) - nextDates.length),
        ].map((date: CalendarDate) => cache(this._renderDateTemplate(date || null)));

        return html` <div class="${baseClassName}-dates" aria-labelledby="id-dialog-label">${dateTemplates}</div> `;
    }

    private _getFirstFocusableDateButtonElement() {
        const { shadowRoot, _baseClassName: baseClassName } = this;

        const selector1 = `.${baseClassName}-date-root:not(.${baseClassName}-date--void)`;
        const selector2 = `.${baseClassName}-date-button`;
        return shadowRoot ? shadowRoot.querySelector<HTMLElement>(`${selector1} ${selector2}`) : null;
    }

    private _renderDateTemplate(cDate: CalendarDate | null) {
        const { type, _baseClassName: baseClassName } = this;
        const dateProps = createCalendarDateProps.call(this, cDate);

        if (dateProps.selected && dateProps.disabled) {
            this.value = null;
            this.dispatchEvent(new CalendarEvent(CalendarEventType.Change));
        }

        let startSelect = false;
        if (cDate && this.startIsSelectedDate && cDate.format(type, false) === this.start) {
            startSelect = true;
        }
        let endSelect = false;
        if (cDate && this.endIsSelectedDate && cDate.format(type, false) === this.end) {
            endSelect = true;
        }
        const [dateBaseClassName, dateRootClasses] = createClassName(`${baseClassName}-date`, {
            void: dateProps.void,
            prev: dateProps.prev, // Date in prev month
            next: dateProps.next, // Date in next month
            today: dateProps.today,
            weekend: dateProps.weekend,
            disabled: dateProps.disabled,
            selected: dateProps.selected,
            start: this.endIsSelectedDate,
            end: this.startIsSelectedDate,
            startNodeInSpan: dateProps.start,
            endNodeInSpan: dateProps.end,
            within: dateProps.within,
            startNode: startSelect,
            endNode: endSelect,
        });

        return html`
            <div class="${dateRootClasses}">
                <div class="${dateBaseClassName}-inner">
                    <button
                        class="${dateBaseClassName}-button"
                        tabindex=${dateProps.focusable ? 0 : -1}
                        aria-label=${this._ariaLabelForDate(cDate)}
                        aria-hidden="${!cDate}"
                        .disabled=${dateProps.disabled}
                        @click=${this._onDateClick}
                        @focus=${this._onDateFocus}
                        @blur=${this._onDateBlur}
                        @mouseover=${this._onMouseover}
                        @mouseout=${this._onMouseout}
                    >
                        <span class="${dateBaseClassName}-label">${cDate ? cDate.getDateString() : ''}</span>
                    </button>
                </div>
            </div>
        `;
    }

    private _ariaLabelForDate(cDate: CalendarDate | null) {
        const { type, _texts } = this;
        const dateAsText = cDate ? cDate.format(type, true) : '';
        if (cDate?.isSelected()) {
            return `${dateAsText} ${_texts.currentlySelected}`;
        }
        return dateAsText;
    }

    private _changeYear(year) {
        const service = this._service;
        const actual = new Date(service.now);

        const newDate = new Date(year, actual.getMonth(), 1);
        this.now = newDate;
        this.dispatchEvent(new CalendarEvent(CalendarEventType.Change));
    }

    private _changeMonthStep(step: number) {
        const service = this._service;
        const actual = new Date(service.now);
        let newDate;
        if (actual.getMonth() === (step < 0 ? 0 : 11)) {
            newDate = new Date(actual.getFullYear() + step, step < 0 ? 11 : 0, 1);
        } else {
            newDate = new Date(actual.getFullYear(), actual.getMonth() + step, 1);
        }
        this.now = newDate;
        this.dispatchEvent(new CalendarEvent(CalendarEventType.Change));
    }

    private _changeMonth(month: number) {
        const service = this._service;
        const actual = new Date(service.now);
        this.now = new Date(actual.getFullYear(), month, 1);
        this.dispatchEvent(new CalendarEvent(CalendarEventType.Change));
    }

    private _goPrevMonth() {
        this._changeMonthStep(-1);
    }

    private _goNextMonth() {
        this._changeMonthStep(1);
    }

    private _isDateWithinStartEnd(date: Date) {
        const { type, start, end } = this;
        const format = formatDateString(date, type);

        const atOrAfterStart = !start || format >= start;
        const atOrAfterEnd = !end || format <= end;
        return atOrAfterStart && atOrAfterEnd;
    }

    private async _focusDateByOffset(offset: number) {
        const { lang, customizeDate } = this;
        const { selectedDate } = this._service;
        let focusedDate = this._focusedDate;

        if (!focusedDate && selectedDate) {
            focusedDate = selectedDate;
        }

        if (focusedDate) {
            const newDate = new Date(focusedDate);
            newDate.setDate(newDate.getDate() + offset);
            const isDisabled = isDateDisabled(newDate, lang, customizeDate);

            // If date at offset is disabled, we have to focus
            // on next/previous focusable date instead.
            if (isDisabled) {
                const isWithinStartEnd = this._isDateWithinStartEnd(newDate);

                if (isWithinStartEnd) {
                    const offsetAdd = offset > 0 ? 1 : -1;
                    this._focusDateByOffset(offset + offsetAdd);
                }
                return;
            }
            await this._focusDate(newDate);
        }
    }

    private async _focusDate(date: Date) {
        const { shadowRoot, _service: service, _baseClassName: baseClassName } = this;
        this.now = new Date(date);
        await this.updateComplete;
        if (shadowRoot) {
            const datesElement = shadowRoot.querySelector(`.${baseClassName}-dates`);
            const offset = service.getDateOffsetOfMonth();
            const index = offset + (date.getDate() - 1);
            const dateElement = datesElement && datesElement.children[index];
            const buttonElement =
                dateElement && dateElement.querySelector<HTMLElement>(`.${baseClassName}-date-button`);

            if (buttonElement) {
                buttonElement.focus();
            }
        }
    }

    private _getDateOfMonthForDateButton(dateButton: HTMLButtonElement) {
        const service = this._service;
        const dateElement = dateButton.parentElement && dateButton.parentElement.parentElement;
        const datesElement =
            dateButton.parentElement &&
            dateButton.parentElement.parentElement &&
            dateButton.parentElement.parentElement.parentElement;
        const dateElements = datesElement ? Array.from(datesElement.children) : [];
        const index = dateElement ? dateElements.indexOf(dateElement) : -1;

        return index - service.getDateOffsetOfMonth() + 1;
    }

    private _onDateClick(e) {
        const dateOfMonth = this._getDateOfMonthForDateButton(e.target);
        const newSelectedDate = new Date(this.now);

        newSelectedDate.setDate(dateOfMonth);

        this.value = newSelectedDate;

        this.dispatchEvent(new CalendarEvent(CalendarEventType.Change));
    }

    private _onDateFocus(e) {
        const dateOfMonth = this._getDateOfMonthForDateButton(e.target);
        const date = new Date(this.now);

        date.setDate(dateOfMonth);
        this._focusedDate = date;
    }

    private _onDateBlur() {
        this._focusedDate = null;
    }

    private _setClassesForStartEndNodesForOngoingSelection(startDiv, stopDiv) {
        const baseClassName = this._baseClassName + '-date';
        const ongoingSelectionWithinClass = `${baseClassName}--ongoingSelectionWithin`;
        const ongoingSelectionEndConnectionClass = `${baseClassName}--ongoingSelectionEndConnection`;

        let foundStop = startDiv === stopDiv;

        let iterDiv = startDiv.nextElementSibling;

        while (iterDiv) {
            if (iterDiv === stopDiv) {
                foundStop = true;
            }
            if (!foundStop) {
                iterDiv.classList.add(ongoingSelectionWithinClass);
            } else {
                // ie11 no support for remove with multiple parameters
                iterDiv.classList.remove(ongoingSelectionWithinClass);
                iterDiv.classList.remove(ongoingSelectionEndConnectionClass);
            }
            iterDiv = iterDiv.nextElementSibling;
        }
        if (stopDiv) {
            stopDiv.classList.add(ongoingSelectionEndConnectionClass);
        }
    }

    private _onMouseover(e) {
        const baseClassName = this._baseClassName + '-date';
        const ongoingSelectionWithinClass = `${baseClassName}--ongoingSelectionWithin`;
        const ongoingSelectionEndConnectionClass = `${baseClassName}--ongoingSelectionEndConnection`;
        const ongoingSelectionStartConnectionClass = `${baseClassName}--ongoingSelectionStartConnection`;

        this._stopMouseOverTimer();

        const button = e.target;
        let endDiv = button.parentElement.parentElement;
        let startDiv = endDiv.parentElement.querySelector(`.${baseClassName}--startNode`);
        let oneDaySpan = false;

        if (this.endIsSelectedDate && !startDiv) {
            startDiv = endDiv;
            endDiv = startDiv.parentElement.querySelector(`.${baseClassName}--endNode`);
            const prevStart = startDiv.parentElement.querySelector(`.${ongoingSelectionStartConnectionClass}`);
            if (prevStart) {
                prevStart.classList.remove(ongoingSelectionStartConnectionClass);
            }
            const withinDivs = startDiv.parentElement.querySelectorAll(`.${ongoingSelectionWithinClass}`);
            withinDivs.forEach(function (div) {
                div.classList.remove(ongoingSelectionWithinClass);
            });
        }

        if (startDiv === endDiv) {
            startDiv.classList.remove(ongoingSelectionWithinClass);
            startDiv.classList.remove(ongoingSelectionEndConnectionClass);
            oneDaySpan = true;
        }

        if (!startDiv && this.startIsSelectedDate) {
            // startDiv is in a previous month...
            startDiv = endDiv.parentElement.querySelector(`.${baseClassName}-root`);
        }

        if (startDiv) {
            startDiv.classList.remove(ongoingSelectionEndConnectionClass);
            if (!oneDaySpan) {
                startDiv.classList.add(ongoingSelectionStartConnectionClass);
            }

            this._setClassesForStartEndNodesForOngoingSelection(startDiv, endDiv);
        }
    }

    private _onMouseout() {
        const baseClassName = this._baseClassName + '-date';

        if (this.shadowRoot) {
            const root = this.shadowRoot.querySelector(`.${baseClassName}s`);
            if (root) {
                this._startMouseOverTimer(function () {
                    const startClass = `${baseClassName}--ongoingSelectionWithin`;
                    const startDiv = root.querySelectorAll(`.${startClass}`);
                    startDiv.forEach(function (div) {
                        div.classList.remove(startClass);
                    });

                    const selectedClass = `${baseClassName}--ongoingSelectionStartConnection`;
                    const selectedDiv = root.querySelector(`.${selectedClass}`);
                    if (selectedDiv) {
                        selectedDiv.classList.remove(selectedClass);
                    }

                    const endClass = `${baseClassName}--ongoingSelectionEndConnection`;
                    const endDiv = root.querySelectorAll(`.${endClass}`);
                    endDiv.forEach(function (div) {
                        div.classList.remove(endClass);
                    });
                });
            }
        }
    }

    private _startMouseOverTimer(handler) {
        this._mouseOutTimer = window.setTimeout(handler, 50);
    }

    private _stopMouseOverTimer() {
        if (this._mouseOutTimer !== -1) {
            window.clearTimeout(this._mouseOutTimer);
            this._mouseOutTimer = -1;
        }
    }
}
